<template>
    <div class="card-view">
        <form class="service-form">
            <h3 class="service-title">{{login_title}}</h3>
            <input v-model="userid" type = "text" :placeholder="placeholderId" name="userid" @input="validate($event)" @keyup.enter="validate($event)">
