    },
  },
  mixins: [consultingStatus],
  mounted() {
    // todo : 로딩 직후 처리할 작업
    console.log('Login.mounted');
  },
  data() {
    return {
        lang: 'kr',
        userid: '',
        password: '',
    };
  },

  computed: {
      login_title(){
          if(this.lang==='kr') return '로그인';
          return 'Login';
      },
      placeholderId(){
          if(this.lang==='kr') return '아이디를 입력하세요';
          return 'Input ID';
      },
      placeholderPw(){
          if(this.lang==='kr') return '비번을 입력하세요';
          return 'Input Password';
      },
      button_title(){
          if(this.lang==='kr') return '제출';
          return 'submit';
      }
    // html 템플릿 안에서 사용된 변수처럼 연산된 결과값으로 사용한다. 
    // 이름을 언급하는것으로 호출
        // parsedData() {
        //   if (this.data.list.length > 5) {
        //     return this.data.list.slice(0, 5);
        //   }

        //   return this.data.list;
        // },
        // show_more() {
        //   return this.data.show_more;
        // },
  },
  filters: {
  },
  methods: {
    validate(event){
        console.log('validate>', event.target.value);

        if (
            this.userid === '' ||
            this.password === ''
        ) 
        {
            console.log('공백발견')
            // $button.removeClass('default');
            // $button.addClass('disabled');
        } 
        else 
        {
            this.login();
        }
    },
    click(id) {
      switch (id) {
        case '로그인':             
